@import url(https://fonts.googleapis.com/css?family=Lobster);
.card {
    border: 2px solid #636363;
    border-radius: 2px;
    width: 150px;
    height: 150px;
    margin: 1rem;
    position: relative;
    box-shadow: 1px 4px 10px #444;
    transition: all .2s ease-in-out
}

.card>.img-container:hover {
    background: hotpink;
}

.card>.img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: pink;
}

.card>.img-container>img {
    width: 100%
}

.card>.content>ul>li {
    margin-top: 1px;
}

ul {
    display: flex;
    align-items: center
}

