nav {
    width: 100%;
    font-size: 25px;
    color: #fff;
    margin-bottom: 50px;
    background: transparent;
    height: 20%;
}

a {
    font-size: 36px;
    color: #fed420;
}

a:hover {
    color: lavender;
    text-decoration: none;
}

nav li {
    display: inline-block;
    padding: 20px 10px;
    width: 30%;
    text-align: center;
}

nav li:hover {
    color: #fed420;
}

ul {
    padding-left: 5px;
    display: flex;
    align-items: center;
}
.wrapper {
    padding-bottom: 50px;
    background-image: url("https://static.zerochan.net/Chibiusa.full.1810481.jpg");
    background-size: cover;
    height: 100%;
    display: ms-flexbox;
    display: flex;
    flex-flow: row wrap;
    -ms-flex-pack: distribute;
    -ms-flex-line-pack: start;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}
.title {
    width: 85%;
    text-align: center;
    font-size: 20px;
    color: #444;
    margin-top: 1%;
    margin-bottom: 30px;
    padding-left: 3%;
    padding-right: 3%;
    flex: 4rem 1;
}
* {
  box-sizing: border-box;
  font-family: Lobster;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
